  2.ËæìÂÖ•Ê°ÜfocusÂêéËÆæÁΩÆËæπÊ°ÜÈ¢úËâ≤Êó†Êïà
  3.Ê≤°ÊúâËæìÂÖ•ÂßìÂêçÔºåÊâÄÊúâÊåâÈíÆÈùûÊ¥ªÊÄß
  -->
  <div>
    <div class="title">ÊàëÁà±Êï∞Â≠¶</div>
    <div class="container font-size18"></div>
    <div class="container">
      <div class="header font-size18">
        <div class="header-above">
          <span for="date">Â≠¶ÁîüÂßìÂêç:</span>
          <span>Âº†ÂòâÁ®ã</span>
          <span for="date">Á≠îÈ¢òÊó•Êúü:</span>
          <span>{{ today }}</span>
          <hr />
        </div>
        <!-- Âá∫È¢òÊï∞ -->
        <div class="group">
          <span>ÊåëÊàò</span>
          <!-- „ÉÜ„Ç≠„Çπ„Éà„Éú„ÉÉ„ÇØ„ÇπÊñπÂºè -->
          <!-- <input
            type="text"
            class="questions-count text-center text-lg-center"
            v-model="questionsCount"
          /> -->
          <select name="count" id="count" v-model="questionsCount">
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="15">15</option>
            <option value="20">20</option>
          </select>
          <span class="font-size18">È¢ò</span>
          <!-- Âá∫È¢ò‰ø°ÊÅØ -->
          <span class="font-size18">
            Â∑≤Âá∫È¢ò:
            <span>{{ totalCount }}</span>
            <span v-show="overFlag">
              Ê≠£Á°ÆÁéáÔºö{{ correctCount }}/{{ totalCount }}
              <!-- Ê≠£Á°Æ:
              <span class="blue">{{ correctCount }}</span>
              ÈîôËØØ:
              <span class="red">{{ totalCount - correctCount }}</span> -->
            </span>
          </span>
        </div>
        <!-- È¢òÁõÆÁ±ªÂûã -->
        <div class="group font-size18">
          È¢òÁõÆÁ±ªÂûã
          <select v-model="questionType">
            <option
              v-for="(item, index) in typeList"
              :key="index"
              :selected="index == 0"
              :value="index"
            >
              {{ item }}
            </option>
          </select>
        </div>
        <!-- È¢òÁõÆÈöæÂ∫¶ -->
        <div class="group font-size18">
          È¢òÁõÆÈöæÂ∫¶
          <select v-model="questionLevel">
            <option
              v-for="(item, index) in levelList"
              :key="index"
              :selected="index == 0"
              :value="index"
            >
              {{ item }}
            </option>
          </select>
        </div>
        <!-- <div class="group">
          <span v-show="overFlag">
            <span class="font-size18"
              >‰Ω†ÁöÑÂàÜÊï∞Ôºö
              <span class="red">{{ scores }}</span>
            </span>
          </span>
        </div> -->
        <div class="font-size18">
          <label class="radio-inline">
            <input
              v-model="model"
              type="radio"
              name="model"
              value="1"
            />ËÆ°Êó∂Ê®°Âºè
          </label>
          <label class="radio-inline">
            <input
              v-model="model"
              type="radio"
              name="model"
              value="2"
            />ÂÄíËÆ°Êó∂Ê®°Âºè
          </label>
        </div>
        <div v-show="model == 2">
          <!-- ËÆæÁΩÆÂÄíËÆ°Êó∂Êó∂Èó¥ -->
          Time:{{ limitTime }}ÂàÜÈíü
          <input type="range" min="1" max="30" v-model="limitTime" />
        </div>
        <div class="button-groups">
          <!-- <button class="btn btn-info" @click="generateQuestions" :disabled="yourName=''">ÈöèÊú∫ÁîüÊàêÈ¢òÁõÆ</button>
          <button class="btn btn-info" @click="resetQuestions" :disabled="yourName=''">Ê∏ÖÁ©∫È¢òÁõÆ</button>-->
          <button class="btn btn-info" @click="generateQuestions">
            ÈöèÊú∫ÁîüÊàêÈ¢òÁõÆ
          </button>
          <button class="btn btn-info" @click="resetQuestions">Ê∏ÖÁ©∫È¢òÁõÆ</button>
          <button
            class="btn btn-danger"
            v-show="model == 1"
            :disabled="!readyFlag"
            @click="start(1)"
          >
            ËÆ°Êó∂Á≠îÈ¢ò
          </button>
          <button
            class="btn btn-danger"
            v-show="model == 2"
            :disabled="!readyFlag"
            @click="start(2)"
          >
            ÂÄíËÆ°Êó∂Á≠îÈ¢ò
          </button>
          <span class="spend-time" id="spendTime">00:00:00</span>
        </div>
      </div>
      <div class="result-area">
        <div class="header-above">
          <span class="result-title">‰ªäÂ§©ÊµãËØïÁªìÊûúÔºà{{ resultCount }}Ôºâ</span>
          <hr />
          <div class="result-content">
            <div
              class="result-single"
              v-for="(item, index) in resultList"
              :key="index"
            >
              {{ item }}
            </div>
          </div>
        </div>
      </div>
      <div class="main-area">
        <table class="table table-striped">
          <thead>
            <tr>
              <th class="col-lg-2 col-xs-2">Â∫èÂè∑</th>
              <th class="col-lg-4 col-xs-6">È¢òÁõÆ</th>
              <th class="col-lg-3 col-xs-2">Á≠îÊ°à</th>
              <th class="col-lg-3 col-xs-2">ÁªìÊûú</th>
            </tr>
          </thead>
          <tbody v-show="questionsArray.length == 0">
            <tr>
              <!-- <td v-if="yourName == ''" class="text-center nodata-message" colspan="4">ËØ∑Â°´ÂÜô‰Ω†ÁöÑÂßìÂêç„ÄÇ</td>
              <td v-else class="text-center nodata-message" colspan="4">{{yourName}}Ôºå‰Ω†Â∑≤ÁªèÂáÜÂ§áÂ•Ω‰∫ÜÂêóÔºü</td>-->
              <td class="text-center nodata-message" colspan="4">
                Âº†ÂòâÁ®ãÔºå‰Ω†Â∑≤ÁªèÂáÜÂ§áÂ•Ω‰∫ÜÂêóÔºü
              </td>
            </tr>
          </tbody>
          <tbody v-show="questionsArray.length">
            <tr v-for="(item, index) in questionsArray" :key="index">
              <td class="col-lg-2 col-xs-2 question">{{ index + 1 }}</td>
              <td class="col-lg-4 col-xs-6 question">{{ item.question }} =</td>
              <td class="col-lg-3 col-xs-2 question">
                <input
                  type="text"
                  :id="index"
                  :disabled="!startFlag"
                  :class="{ 'answer-boder': startFlag, 'answer-input': true }"
                />
              </td>
              <td class="col-lg-3 col-xs-2 question">
                <div>
                  <span v-if="item.checkResult == 1">üòä</span>
                  <span v-else-if="item.checkResult == 0">üò°</span>
                  <span v-else>‚ùî</span>
                  <!-- <span v-show="item.checkResult==1">111</span>
                  <span v-show="item.checkResult==0">222</span>
                  <span v-show="item.checkResult==2">333</span>-->
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <button
        v-show="startFlag"
        class="btn btn-lg btn-danger"
        @click="submit"
        :disabled="overFlag"
      >
        ÊàëÂÅöÂÆåÂπ∂‰∏îÊ£ÄÊü•ÂÆå‰∫ÜÔºåÊàëË¶ÅÊèê‰∫§ÔºÅÔºÅÔºÅ
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      message: "È†ëÂºµ„Å£„Å¶„Åè„Å†„Åï„ÅÑ„Å≠ÔºÅ",
      yourName: "",
      timmer: "",
      hour: 0,
      minite: 0,
      seconds: 0,
      // ÂÄíËÆ°Êó∂Êó∂Èó¥(minutes)
      limitTime: 1,
      // ÂÄíËÆ°Êó∂Êó∂Èó¥(seconds)
      limitTimeSeconds: 60,
      // Á≠îÈ¢òÊ®°Âºè
      model: "1",
      // Á≠îÈ¢òÂÆåÊàêÊ†áËØÜ
      overFlag: false,
      // Âá∫È¢òÂÆåÊØïÊ†áËØÜ
      readyFlag: false,
      // ÂºÄÂßãÁ≠îÈ¢òÊ†áËØÜ
      startFlag: false,
      // ‰∏ÄÊ¨°ÁîüÊàêÈ¢òÁõÆÊï∞
      questionsCount: 5,
      // Âá∫È¢òÊÄªÊï∞
      totalCount: 0,
      // ‰ΩúÁ≠îÊ≠£Á°ÆÊï∞
      correctCount: 0,
      // ÂæóÂàÜ
      scores: 0,
      // ÈÄâÊã©È¢òÁõÆÁ±ªÂûã
      questionType: 0,
      // ÈÄâÊã©È¢òÁõÆÈöæÂ∫¶
      questionLevel: "0",
      // È¢òÁõÆÁ±ªÂûãÂàóË°®
      typeList: ["Âä†Ê≥ï", "ÂáèÊ≥ï", "Âä†ÂáèÊ∑∑Âêà"],
      // È¢òÁõÆÈöæÂ∫¶ÂàóË°®
      levelList: ["10‰ª•ÂÜÖ", "20‰ª•ÂÜÖ", "50‰ª•ÂÜÖ", "100‰ª•ÂÜÖ"],
      // È¢òÁõÆÂàóË°®
      questionsArray: [],
      // ‰ªäÊó•ÊàêÁª©
      resultList: [],
      // ‰ªäÊó•Âá∫È¢ò‰∏™Êï∞
      resultCount: 0,
    };
  },
  methods: {
    submit() {
      // stop timmer
      this.stopTimer();
      this.startFlag = false;
      this.overFlag = true;
      for (var i = 0; i < this.totalCount; i++) {
        // $("#intro")
        let zjcAnswer = document.getElementById(i).value;
        // Ê≠£Á°Æ
        if (zjcAnswer != "" && this.questionsArray[i].answer == zjcAnswer) {
          this.questionsArray[i].checkResult = 1;
          this.correctCount++;
        } else {
          // ÈîôËØØ
          this.questionsArray[i].checkResult = 0;
        }
      }
      // scores
      this.scores = (this.correctCount / this.totalCount) * 100;
      // ‰ªäÊó•ÊµãËØïÊ¨°Êï∞
      this.resultCount++;
      //
      this.resultList.push(this.scores);
    },
    // ÈöèÊú∫ÁîüÊàêÈ¢òÁõÆ
    generateQuestions() {
      // 0:"10‰ª•ÂÜÖ", 1:"20‰ª•ÂÜÖ", 2:"50‰ª•ÂÜÖ", 3:"100‰ª•ÂÜÖ"
      let level = this.questionLevel;
      let levelNum;
      switch (level) {
        case 0:
          levelNum = 10;
          break;
        case 1:
          levelNum = 20;
          break;
        case 2:
          levelNum = 50;
          break;
        case 3:
          levelNum = 100;
          break;
        default:
          levelNum = 10;
          break;
      }
      // 0Ôºö"Âä†Ê≥ï",1Ôºö"ÂáèÊ≥ï", 2Ôºö"Âä†ÂáèÊ∑∑Âêà"
      let type = this.questionType;
      let questionStr = "";

      this.readyFlag = true;
      this.startFlag = false;
      this.overFlag = false;

      let operatorIndex, num1, num2, num3, ope1, ope2;
      for (var i = 0; i < this.questionsCount; i++) {
        // mix
        operatorIndex = Math.round(Math.random());
        num1 = Math.ceil(Math.random() * levelNum);
        num2 = Math.ceil(Math.random() * levelNum);
        num3 = Math.ceil(Math.random() * levelNum);
        ope1 = ["+", "-"][operatorIndex];
        ope2 = ["+", "-"][operatorIndex];
        switch (type) {
          case 0:
            // plus
            questionStr = num1 + " + " + num2;
            break;
          case 1:
            // minus
            if (num1 > num2) {
              questionStr = num1 + " - " + num2;
            } else {
              questionStr = num2 + " - " + num1;
            }
            break;
          case 2:
            // mix
            if (ope1 == "-" && num1 < num2) {
              num1 = this.changeNum(num1, num2)[0];
              num2 = this.changeNum(num1, num2)[1];
              // num2 = Math.ceil(Math.random() * num1);
            }
            if (ope2 == "-" && eval(num1 + ope1 + num2) < num3) {
              num3 = Math.ceil(Math.random() * eval(num1 + ope1 + num2));
            }
            questionStr =
              num1 + " " + ope1 + " " + num2 + " " + ope2 + " " + num3;
            break;
        }
        this.totalCount++;
        this.questionsArray.push({
          // question: num1 + " " + ope1 + " " + num2 + " " + ope2 + " " + num3,
          question: questionStr,
          answer: eval(questionStr),
          checkResult: 2,
        });
      }
    },
    // ‰∫§Êç¢value1Âíåvalue2ÁöÑÂÄº
    changeNum(value1, value2) {
      return [value2, value1];
    },
    // ÈáçÊñ∞ÁîüÊàêÈ¢òÁõÆ
    resetQuestions() {
      this.readyFlag = false;
      this.startFlag = false;
      this.overFlag = false;
      this.totalCount = 0;
      this.correctCount = 0;
      this.scores = 0;
      this.questionsArray = [];
      this.stopTimer();
      document.getElementById("spendTime").innerHTML = "00:00:00";
    },
    // ÂºÄÂßãÁ≠îÈ¢ò
    start(model) {
      // ÂÄíËÆ°Êó∂ÁßíÊï∞
      this.limitTimeSeconds = this.limitTime * 60;
      this.startFlag = true;
      this.overFlag = false;
      this.readyFlag = false;

      // ËÆ°Êó∂ÂºÄÂßã
      if (this.timmer != "") {
        this.stopTimer();
      }
      // init
      this.hour = 0;
      this.minite = 0;
      this.seconds = 0;

      // ËÆ°Êó∂Ê®°Âºè
      if (model == 1) {
        this.timmer = setInterval(this.startTimer, 1000);
      } else if (model == 2) {
        // ÂÄíËÆ°Êó∂Ê®°Âºè
        this.timmer = setInterval(this.countDown, 1000);
      }
    },
    startTimer() {
      this.seconds++;
      if (this.seconds == 60) {
        this.seconds = 0;
        this.minite++;
      }
      if (this.minite == 60) {
        this.minite = 0;
        this.hour++;
      }
      document.getElementById("spendTime").innerHTML =
        (this.hour < 10 ? "0" + this.hour : this.hour) +
        ":" +
        (this.minite < 10 ? "0" + this.minite : this.minite) +
        ":" +
        (this.seconds < 10 ? "0" + this.seconds : this.seconds);
    },
    stopTimer() {
      clearInterval(this.timmer);
    },
    //ÈúÄË¶Å‰º†ÂÖ•ÁöÑÂèÇÊï∞‰∏∫ÁßíÊï∞ÔºåÊ≠§ÊñπÊ≥ïÂÄíËÆ°Êó∂ÁªìÊùüÂêé‰ºöËá™Âä®Âà∑Êñ∞È°µÈù¢
    countDown() {
      var t = this.limitTimeSeconds;
      // ÂÄíËÆ°Êó∂ÁªìÊùü
      if (t == 0) {
        // this.stopTimer();
        this.submit();
      }
      console.log("t=" + t);
      this.hour = Math.floor((t / 60 / 60) % 24);
      this.hour < 24 && (this.hour = "0" + this.hour);
      this.minite = Math.floor((t / 60) % 60);
      this.minite < 10 && (this.minite = "0" + this.minite);
      this.seconds = Math.floor(t % 60);
      this.seconds < 10 && (this.seconds = "0" + this.seconds);
      document.getElementById("spendTime").innerHTML =
        this.hour + ":" + this.minite + ":" + this.seconds;
      console.log(this.hour + ":" + this.minite + ":" + this.seconds);
      this.limitTimeSeconds--;
    },
  },
  computed: {
    today() {
      let myDate = new Date();
      let year = myDate.getFullYear();
      let month = myDate.getMonth() + 1;
      let day = myDate.getDate();
      return year + "/" + month + "/" + day;
    },
  },
  filters: {
    standerAnswer(value) {
      return eval(value);
    },
  },
};
</script>

<style src="@/assets/css/common.css"></style>




